import React from 'react'

import CustomInput from '../custom-input/custom-input.component'
import CustomTextarea from '../custom-textarea/custom-textarea.component'
import ProductInputRow from '../product-input-row/product-input-row.component'

import {
    FormCheckbox,
    TextareaRow,
    CustomSelect,
    Title
} from './product-glasses-form.styles.jsx'

const ProductGlassesForm = ({ product, handleChange, handleSizeChange, handleSpecsChange }) => {

    return (
        <div>
            <h3>Základné informacie</h3>
            <ProductInputRow
                label="Unikátny kód produktu"
                example="napr: TH1590807"
            >
                <CustomInput
                    label="Ean kód*"
                    type='text'
                    name='eanCode'
                    value={product.eanCode}
                    handleChange={(e) => handleChange(e)}
                    required
                />
            </ProductInputRow>
            <ProductInputRow
                label="Názov produktu"
                example="napr: Tommy Hilfiger"
            >
                <CustomInput
                    label="Názov*"
                    type='text'
                    name='name'
                    value={product.name}
                    handleChange={(e) => handleChange(e)}
                    required

                />
            </ProductInputRow>
            <ProductInputRow
                label="Značka"
                example="napr: Tommy Hilfiger"
            >
                <CustomInput
                    label="Značka"
                    type='text'
                    name='brand'
                    value={product.brand}
                    handleChange={(e) => handleChange(e)}
                />
            </ProductInputRow>
            <ProductInputRow
                label="Cena v centoch"
                example="napr: 1055 = 10,55€"
            >
                <CustomInput
                    label="Cena*"
                    type='number'
                    name='price'
                    value={product.price.toString()}
                    handleChange={(e) => handleChange(e)}
                    required

                />
            </ProductInputRow>
            <TextareaRow>
                <div>
                    <CustomTextarea
                        label="Popis"
                        name='description'
                        rows='5'
                        value={product.description}
                        handleChange={(e) => handleChange(e)}
                    />
                </div>
                <div>
                    <p>Popis produktu</p>
                </div>
            </TextareaRow>



            {
                !(product.type === 5 || product.type === 4) && (
                    <React.Fragment>
                        <h3>Špecifikacie</h3>
                        <ProductInputRow
                            label="Farebný kód produktu"
                            example="napr: #000000"
                        >
                            <CustomInput
                                label="Color code"
                                type='text'
                                name='colorCode'
                                value={product.colorCode}
                                handleChange={(e) => handleChange(e)}
                            />
                        </ProductInputRow>
                        <ProductInputRow
                            label="Pohlavie pre ktoré je produkt určený"
                            example=""
                        >
                            <CustomSelect name='sex' value={product.specs.sex} onChange={e => handleSpecsChange(e)}>
                                <option value='M'>Muži</option>
                                <option value='W'>Ženy</option>
                                <option value='U'>Nezáleží</option>
                            </CustomSelect>
                        </ProductInputRow>

                        <ProductInputRow
                            label="Farba"
                            example="napr: Čierna"
                        >
                            <CustomInput
                                label="Farba rámu"
                                type='text'
                                name='frameColor'
                                value={product.specs.frameColor}
                                handleChange={(e) => handleSpecsChange(e)}
                            />
                        </ProductInputRow>
                        <ProductInputRow
                            label="Materiál"
                            example="napr: Titán"
                        >
                            <CustomInput
                                label="Materiál rámu"
                                type='text'
                                name='frameMaterial'
                                value={product.specs.frameMaterial}
                                handleChange={(e) => handleSpecsChange(e)}
                            />
                        </ProductInputRow>
                        <ProductInputRow
                            label="Tvar"
                            example="napr: Okruhlý"
                        >
                            <CustomInput
                                label="Tvar rámu"
                                type='text'
                                name='frameStyle'
                                value={product.specs.frameStyle}
                                handleChange={(e) => handleSpecsChange(e)}
                            />
                        </ProductInputRow>
                        <ProductInputRow
                            label="Farba skla"
                            example="napr: Priehľadná"
                        >
                            <CustomInput
                                label="Farba"
                                type='text'
                                name='lensColor'
                                value={product.specs.lensColor}
                                handleChange={(e) => handleSpecsChange(e)}
                            />
                        </ProductInputRow>

                        <Title>Velikosť</Title>

                        {product.specs.size.map((value, idx) => {
                            return (
                                <ProductInputRow
                                    key={idx}
                                    label={`Velkosť`}
                                    example="napr: 0"
                                >
                                    <CustomInput
                                        label={`${idx}`}
                                        type='number'
                                        // name={store.name}
                                        value={value.toString()}
                                        handleChange={e => handleSizeChange(e, idx)}
                                    />
                                </ProductInputRow>
                            )
                        })}
                    </React.Fragment>
                )
            }
        </div>
    )
}

export default ProductGlassesForm
